* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

html {
    background-color: #121212;
}
#root {
    display: flex;
    flex-direction: column;
    align-items: center;
}
main {
    flex: 1 0 auto;
}
button {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
}
ul {
    list-style-type: none;
}
input {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    outline: inherit;
    color: white;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}
hr {
    border: 1px solid #c4c4c4;
}

.header {
    display: grid;
    grid-template-rows: 2.5fr 1fr;
    grid-template-columns: 14fr 1fr;
    color: white;
    background-color: #2e2e2e;
}
.header > h1 {
    margin: 1.5rem 0 0.5rem 2rem;
    align-self: center;
}
.header > button {
    justify-self: center;
    align-self: center;
    height: 3.5rem;
    width: 3.5rem;
    border-radius: 50%;
    grid-row: span 2;
    box-shadow: 0 0 1rem #6b6b6b;
    transition: all 0.5s;
}
.header > button:hover {
    box-shadow: 0 0 1rem #acacac;
    transform: scale(1.2);
}
.header > p {
    margin: 0  0 0 2rem;
    color: #c4c4c4
}
.footer {
    display: flex;
    justify-content: space-between;
    color: #c4c4c4;
}
.footer > p {
    margin: 2rem;
}
.content {
    margin: 0 2rem 0 2rem;
    min-height: 85vh;
}

.bill {
    height: 50px;
    width: 50px;
}

.teams-container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 1.5rem 0 1.5rem 0;
}
.team-pfp {
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    box-shadow: 0 0 1rem #3f3f3f;
    transition: all 0.5s;
}
.team-pfp:hover {
    box-shadow: 0 0 1rem #818181;
    height: 5.5rem;
    width: 5.5rem;
}
.team-pfp > img {
    height: 100%;
    width: 100%;
    border-radius: 50%;
}
.team-button {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #2e2e2e;
    padding: 2rem;
    margin: 1rem;
    border-radius: 5%;
    transition: all 0.5s;
}
.team-button:not(.selected):hover {
    background-color: #3d3d3d;
}
.selected:hover {
    background-color:  #4100d8;
}
.selected {
    background-color: #3700B3;
}
.team-money {
    display: flex;
    flex-wrap: wrap;
    max-width: 250px;
}
.team-name {
    display: inline;
    font-size: 2.5rem;
    margin: 10px 0 10px 0;
    width: 350px;
}
.team-inv {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    max-width: 250px;
}
.inv-img {
    height: 50px;
    width: 50px;
}

.cards-container {
    display: flex;
    flex-wrap: wrap;
    margin: 1.5rem 0 1.5rem 0;
}
.item-container {
    box-sizing: border-box;
    width: 300px;
    margin: 1rem;
    background-color: #2e2e2e;
    border-radius: .5rem;
    color: white;
    transition: all 0.5s;
}
.itm-img {
    width: 100%;
    height: 40%px;
    border-radius: .5rem 0 .5rem 0;
}
.itm-details {
    margin: 20px 20px 0 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1rem;
}
.item-money {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    max-width: 250px;
    justify-content: center;
    margin: 1rem 0 1rem 0;
}
.itm-label {
    font-size: 1.75rem;
}
.itm-desc {
    font-size: 0.9rem;
    font-weight: 100;
    margin: 0.5rem 0 1.5rem 0;
    text-align: left;
}
.itm-qty , .itm-num {
    font-size: 0.8rem;
    color: #d6d5d5;
}
.itm-dir {
    font-size: 0.8rem;
    color: #c4c4c4;
    margin-bottom: 10px;
}
.item-container:disabled {
    background-color: #ff0000;
    opacity: 50%;
}
.item-container:disabled, .item-container:disabled:hover {
    cursor: not-allowed;
}
.item-container:not(:disabled):hover {
    background-color: #3d3d3d;
}

/* custom scrollbar */
::-webkit-scrollbar {
  width: 20px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #7c7c7c;
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #aaaaaa;
}